.logos-list {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;

	@include min-w(md) {
		margin: 0 -15px;
	}

	&__item {
		flex: 0 0 90px;
    max-width: 90px;

		@include min-w(sm) {
			flex-basis: 0;
			flex-grow: 1;
			min-width: 0;
			max-width: 100%;
		}

		@include min-w(md) {
			flex: 0 0 25%;
			max-width: 25%;
			padding: 0 15px;
		}

		@include min-w(lg) {
			flex: 0 0 16.66666666%;
			max-width: 16.66666666%;
		}
	}
}

.logo-item {
	@include flexCenter;

	height: 40px;
	padding: 0 10px;
	margin: 5px 0;

	@include min-w(sm) {
		padding: 10px 15px;
		margin: 10px 0;
		height: 60px;
	}

	@include min-w(md) {
		padding: 10px 20px;
		height: 80px;
	}

	@include min-w(xl) {
		padding: 10px 30px;
	}

	img {
		max-height: 100%;
		flex-shrink: 0;
	}
}