.progress-bar {
	position: relative;
	overflow: hidden;
	height: 35px;
	background-color: lighten($gray-400, 5%);
	border-radius: 44px;
	padding: 5px;

	@include min-w(lg) {
		height: 44px;
		padding: 8px;
	}

	&__inner {
		position: absolute;
		top: 5px;
		left: 5px;
		bottom: 5px;
		max-width: calc(100% - 10px);
		width: 100%;
		background-color: $primary;
		border-radius: 44px;
		animation: progress-bar-stripes 2s linear infinite;
		background-image: linear-gradient(-45deg, darken($primary, 10%) 25%, transparent 25%, transparent 50%, darken($primary, 10%) 50%, darken($primary, 10%) 75%, transparent 75%, transparent);
		background-size: 40px 40px;
		transition: width 0.3s;

		@include min-w(lg) {
			top: 8px;
			left: 8px;
			bottom: 8px;
			max-width: calc(100% - 16px);
		}
	}
}

@keyframes progress-bar-stripes {
	from {background-position: 0 0;}
	to {background-position: 40px 0;}
}

.progress-bar-block {
	@include min-w(sm) {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-wrap: wrap;
	}

	&__progress-bar {
		flex-grow: 1;
		margin-bottom: 5px;

		@include min-w(sm) {
			padding-right: 15px;
			margin-bottom: 0;
		}

		@include min-w(md) {
			padding: 0 15px;
		}

		@include min-w(lg) {
			padding: 0 30px;
		}
	}

	&__left-title, &__time {
		margin-bottom: 0;
		font-family: $font-family-sans-serif;
		font-weight: 300;
		font-size: 18px;

		@include min-w(lg) {
			font-size: 24px;
		}
	}

	&__left-title {
		width: 100%;
		text-align: center;
		margin-bottom: 10px;

		@include min-w(md) {
			width: auto;
			text-align: initial;
			margin-bottom: 0;
		}
	}

	&__time {
		min-width: 85px;
		flex-shrink: 1;
		text-align: center;

		@include min-w(sm) {
			text-align: initial;
		}
	}
}