.footer {
	&__top-content {
		padding: 60px 0 10px;
		background-color: $gray-900;

		@include min-w(sm) {
			padding: 100px 0 50px;
		}

		@include min-w(lg) {
			padding: 120px 0;
		}

		@include min-w(xl) {
			padding: 180px 0;
		}
	}

	&__bottom-content {
		padding: 20px 0;
		background-color: $black;

		@include min-w(sm) {
			padding: 50px 0;
		}

		@include min-w(xl) {
			padding: 85px 0;
		}
	}
}

.footer-list-block {
	color: $white;
	margin-bottom: 50px;

	@include max-w(sm) {
		text-align: center;
	}

	@include min-w(lg) {
		margin-bottom: 0;
	}

	&__title {
		font-family: $font-family-sans-serif;
		margin-bottom: 30px;
	}
}

.footer-list {
	font-size: 16px;

	&.-with-arrow {
		.footer-list__link {
			position: relative;
			padding-left: 30px;

			&:before {
				content: "\f061";
				font-family: 'Font Awesome 5 Free';
				font-weight: 900;
				position: absolute;
				top: 5px;
				left: 0;
				font-size: 10px;
				transition: left 0.3s;
			}

			&:hover {
				&:before {
					left: 5px;
				}
			}
		}

		.footer-list__item {
			&:not(:last-child) {
				margin-bottom: 10px;
			}
		}
	}

	&__link {
		display: inline-block;
		color: $white;
		transition: color 0.3s;
		font-weight: 300;

		&:hover {
			color: $primary;
		}
	}
}