.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: $zindex-fixed;
	background-color: $black;
	padding: 12px 15px;

	@include min-w(lg) {
		padding: 12px 40px;
	}

	&__holder {
		display: flex;
		align-items: center;
		justify-content: space-between;

	}

	&__logo {
		display: block;
		max-width: 150px;
		width: 100%;
		font-size: 0;
		margin-right: 20px;

		@include min-w(md) {
			max-width: 240px;
		}
	}

	&__btn {
		white-space: nowrap;
		display: none;

		@include min-w(md) {
			display: inline-block;
		}
	}

	&__hamburger {
		@include min-w(md) {
			display: none;
		}
	}

	&__mobile-menu {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		display: none;
		max-height: calc(100vh - 49px);
		overflow: auto;

		@include min-w(md) {
			display: none;
		}
	}
}