// COLORS
$white:    #ffffff;
$gray-100: #f8f9fa;
$gray-200: #e9ecef;
$gray-300: #f7f7f7;
$gray-400: #ced4da;
$gray-500: #adb5bd;
$gray-600: #6c757d;
$gray-700: #495057;
$gray-800: #353535;
$gray-900: #1b1c1e;
$black:    #000000;

$blue:    #497de3;
$blue-dark:    #2032ff;
$indigo:  #6610f2;
$purple:  #6f42c1;
$pink:    #e83e8c;
$red:     #dc3545;
$orange:  #fd7e14;
$yellow:  #f0e840;
$green:   #00e261;
$teal:    #20c997;
$cyan:    #17a2b8;

$primary:       $blue;
$secondary:     $gray-600;
$success:       $green;
$info:          $cyan;
$warning:       $yellow;
$danger:        $red;
$light:         $gray-100;
$dark:          $gray-800;


// MAIN
$body-bg:                   $white;
$body-color:                $black;
$enable-rounded:            true;

// TYPOGRAPHY
$font-family-sans-serif:    "SalvoSans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$font-family-heading:       "FuturaLTCondense", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$font-family-monospace:     SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
$font-family-base:          $font-family-sans-serif;

$font-size-base:            1.4375rem;
$font-weight-base:          400;
$line-height-base:          1.8;

$paragraph-margin-bottom:   20px;

$headings-font-family:      $font-family-heading;
$headings-font-weight:      800;
$headings-line-height:      1.2;
$headings-color:            inherit;

$font-size-sm:                16px !default;
$line-height-sm:              1.5 !default;

$font-size-lg:                $font-size-base * 1.25 !default;
$line-height-lg:              1.5 !default;

// Headers size
$title-palette: (
	xs: (
		'h1': 36,
		'h2': 28,
		'h3': 18,
		'h4': 16,
		'h5': 16,
		'h6': 16
	),
	sm: (
		'h1': 36,
		'h2': 30,
		'h3': 22,
		'h4': 20,
		'h5': 18,
		'h6': 16
	),
	md: (
		'h1': 36,
		'h2': 32,
		'h3': 28,
		'h4': 24,
		'h5': 18,
		'h6': 16
	),
	lg: (
		'h1': 50,
		'h2': 38,
		'h3': 28,
		'h4': 26,
		'h5': 22,
		'h6': 16
	),
	xl: (
		'h1': 90,
		'h2': 46,
		'h3': 28,
		'h4': 26,
		'h5': 22,
		'h6': 16
	)
);

// GRID
$grid-gutter-width: 30px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px);
$container-max-widths: (sm: 540px, md: 720px, lg: 960px, xl: 1140px);

// LINKS
$link-color:                              $primary;
$link-decoration:                         none;
$link-hover-color:                        darken($link-color, 15%);
$link-hover-decoration:                   none;

// BUTTONS
$input-btn-padding-y:         .375rem;
$input-btn-padding-x:         1.5rem;
$input-btn-font-size:         $font-size-base;
$input-btn-line-height:       $line-height-base;
$input-btn-border-width:      1px;
$btn-border-radius:           0.25rem;
$btn-transition:              color .3s ease-in-out, background-color .3s ease-in-out, border-color .3s ease-in-out, box-shadow .3s ease-in-out;

$input-btn-padding-y-sm:      .3rem !default;
$input-btn-padding-x-sm:      .75rem !default;
$input-btn-font-size-sm:      $font-size-sm !default;
$input-btn-line-height-sm:    $line-height-sm !default;

$input-btn-padding-y-lg:      .5rem !default;
$input-btn-padding-x-lg:      30px !default;
$input-btn-font-size-lg:      $font-size-lg !default;
$input-btn-line-height-lg:    $line-height-lg !default;

// Z-INDEX MASTER LIST
$zindex-dropdown:                   1000;
$zindex-sticky:                     1020;
$zindex-under-fixed:                1025;
$zindex-fixed:                      1030;
$zindex-modal-backdrop:             1040;
$zindex-modal:                      1050;
$zindex-popover:                    1060;
$zindex-tooltip:                    1070;

// FONT AWESOME FONT PATH =================================
$fa-font-path: "fonts/font-awesome";

// SLICK SLIDER THEME (OPTIONAL) =================================
$slick-font-family: "Font Awesome 5 Free";
$slick-arrow-color: $primary;
$slick-prev-character: "\f0a8";
$slick-next-character: "\f0a9";
$slick-dot-character: "\f111";
$slick-dot-color: $primary;
$slick-dot-color-active: $slick-dot-color;
$slick-dot-size: 10px;
$slick-opacity-default: 1;
$slick-opacity-on-hover: 1;
$slick-opacity-not-active: 0.25;

// custom

// arrow
$slick-arrow-size: 1.5rem;
$slick-arrow-weight: 900;
$slick-arrow-width: 25px;
$slick-arrow-height: $slick-arrow-width;
$slick-arrow-bg: transparent;
// dots
$slick-dots-width: 15px;
$slick-dots-height: $slick-dots-width;
$slick-dots-weigth: 900;
$slick-dots-margin-left-right: 5px;