.hamburger {
	cursor: pointer;

	&__line {
		width: 25px;
		height: 3px;
		border-radius: 3px;
		background-color: $white;
		display: block;
		margin: 4px auto;
		transition: all 0.3s ease-in-out;
	}

	&.-active {
		.hamburger__line {
			&:nth-child(1) {
				transform: translateY(7px) rotate(45deg);
			}

			&:nth-child(2) {
				opacity: 0;
			}

			&:nth-child(3) {
				transform: translateY(-7px) rotate(-45deg);
			}
		}
	}
}