.quiz-form {

	&:not([novalidate="novalidate"]) {
		display: none;
	}

	// top navigation
	.steps {
		ul[role="tablist"] {
			display: flex;
			justify-content: center;

			.current-info {
				display: none !important;
			}

			li {
				position: relative;
				flex-basis: 85px;
				flex-grow: 1;
				min-width: 0;
				max-width: 85px;

				&.disabled {
					a {
						cursor: default !important;
					}
				}

				&:not(:last-child) {
					&:after {
						content: '';
						display: block;
						height: 2px;
						background-color: $gray-400;
						position: absolute;
						top: 50%;
						left: 50%;
						width: 100%;
						transform: translate(0, -50%);
					}
				}

				&.error {
					a {
						background-color: $red !important;
					}
				}

				&.done {
					a {
						background-color: lighten($primary, 15%);
						transition: color 0.3s, background-color 0.3s;

						&:hover {
							background-color: $primary;
						}
					}
				}

				&.current {
					a {
						cursor: default;
						background-color: $primary;
					}
				}
			}

			a {
				@include sizeElement(16px);
				@include flexCenter;

				@include min-w(400) {
					@include sizeElement(18px);
				}

				@include min-w(md) {
					@include sizeElement(22px);
				}

				position: relative;
				z-index: 5;
				border-radius: 50%;
				margin: 0 auto;
				background-color: $gray-400;
				color: $white;
				transition: color 0.3s, background-color 0.3s;
			}

			.number {
				font-size: 10px;
			}
		}
	}

	// content
	.content {
		padding: 50px 0;

		.title {
			display: none;
		}

		.step-section {
			max-width: 880px;
			margin: 0 auto;

			.step-title {
				text-align: center;
			}

			fieldset {
				input[type='text'],
				input[type='tel'],
				input[type='email'],
				input[type='search'],
				input[type='password'],
				input[type='url'],
				textarea {

					@include min-w(sm) {
						margin-left: 7px;
						margin-right: 7px;
						width: calc(100% - 14px);
					}

					&:not(:last-child) {
						margin-bottom: 10px;

						@include min-w(sm) {
							margin-bottom: 14px;
						}
					}
				}

				.standart-checkbox,
				.standart-radio {
					margin: 0 0 10px;

					@include min-w(sm) {
						padding: 0 7px;
						float: left;
						width: 50%;
						margin: 0 0 14px;
					}
				}

				.checkbox-with-icon,
				.radio-with-icon {
					margin: 0 0 10px;

					@include min-w(sm) {
						width: 50%;
						padding: 0 7px;
						margin: 0 0 14px;
						float: left;
					}

					@include min-w(md) {
						width: 25%;
					}
				}

				.irs--flat {
					margin: 0 7px 14px;
				}

				&.error {
					.standart-checkbox,
					.standart-radio,
					.checkbox-with-icon,
					.radio-with-icon {
						&__item {
							background-color: $red;
						}
					}
				}
			}

			label.error {
				display: block;
				color: $red;
				text-align: center;
				font-size: 16px;
				margin-bottom: 0;
			}
		}
	}

	// bottom buttons
	.actions {
		ul[role="menu"] {
			display: flex;
			align-items: center;
			justify-content: center;

			li {
				margin: 5px;

				&.disabled {
					a {
						background-color: $gray-400;
						border-color: $gray-400;
						cursor: default !important;

						&:active {
							background-color: $gray-400 !important;
							border-color: $gray-400 !important;
						}
					}
				}
			}

			a {
				@extend .btn;
				@extend .btn-primary;

				min-width: 150px;
			}
		}
	}
}