@include title-size();

.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

body {
	@include max-w(xl) {
		font-size: 20px;
	}

	@include max-w(lg) {
		font-size: 18px;
	}

	@include max-w(md) {
		font-size: 16px;
	}
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	mark {
		background-color: transparent;
		color: $yellow;
		padding: 0;
	}
}

h1, .h1 {
	text-transform: uppercase;
	margin-bottom: 20px;

	@include min-w(md) {
		margin-bottom: 30px;
	}

	@include min-w(lg) {
		line-height: 1.1;
		letter-spacing: -1px;
	}

	@include min-w(xl) {
		letter-spacing: -2px;
	}

	&.smaller {
		@include min-w(xl) {
			font-size: 75px;
		}
	}
}

h2, .h2 {
	text-transform: uppercase;
	margin-bottom: 30px;
	letter-spacing: -1px;

	@include min-w(md) {
		letter-spacing: -2px;
	}

	@include min-w(lg) {
		letter-spacing: -3px;
	}
}

h3, .h3 {
	margin-bottom: 20px;
}

h4, .h4 {
	margin-bottom: 20px;
}

h5, .h5 {
	margin-bottom: 20px;
}

h6, .h6 {
	margin-bottom: 20px;
}

.section-title {
	max-width: 700px;
	margin: 0 auto;
	text-align: center;
	margin-bottom: 30px;

	@include min-w(md) {
		margin-bottom: 50px;
	}

	@include min-w(xl) {
		margin-bottom: 100px;
	}
}

.description-text {
	font-size: 16px;
	margin-bottom: 40px;

	@include min-w(lg) {
		font-size: 18px;
	}

	@include min-w(xl) {
		font-size: 20px;
	}
}

.warning-text {
	color: $green;
	font-weight: 700;
}

.subtitle {
	text-transform: uppercase;
	font-family: $font-family-sans-serif;
	font-weight: 300;
	letter-spacing: 1px;
	margin-bottom: 15px;
}

.wp-wysiwyg {
	font-weight: 300;

	strong {
		font-weight: 700;
	}

	img {
		display: block;

		.aligncenter {
			margin: 0 auto;
		}

		.alignleft {
			@include min-w(md) {
				float: left;
				width: 50%;
				margin: 10px 30px 10px 0;
			}
		}

		.alignright {
			@include min-w(md) {
				float: right;
				width: 50%;
				margin: 10px 0 10px 30px;
			}
		}
	}

	ul {
		margin: 20px 0;
		list-style-type: disc;
		list-style-position: inside;
	}

	ol {
		margin: 20px 0;
		padding-left: 0;
		list-style-position: inside;
	}

	blockquote {
		margin: 40px 0;
		padding-left: 50px;
		border-left: 2px solid $black;

		p {
			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	a {
		&:not([class]) {
			color: $primary;
			transition: color 0.3s;

			&:hover {
				color: darken($primary, 10%);
			}
		}
	}

	del {
		text-decoration: line-through;
	}

	hr {
		margin: 30px 0;
		border-color: rgba($black, 0.2);
	}
}