.section {
	padding: 50px 0;

	@include min-w(sm) {
		padding: 70px 0;
	}

	@include min-w(xl) {
		padding: 100px 0;
	}

	&-welcome {
		display: flex;
		flex-direction: column;

		&__content {
			@include flexCenter;

			background-color: $black;
			color: $white;

			@include min-w(md) {
				min-height: 100vh;
			}

			&.-with-black-overlay {
				&:after {
					@include absoluteCover;

					content: '';
					display: block;
					background-color: rgba($black, 0.8);
				}
			}
		}

		&__content-inner {
			position: relative;
			z-index: 5;
			text-align: center;
			width: 100%;
			padding: 80px 0 50px;

			@include min-w(md) {
				padding: 120px 0;
			}

			@include min-w(xl) {
				padding: 150px 0;
			}

			h2, .h2 {
				@include max-w(sm) {
					font-size: 30px;
				}
			}
		}

		&__video-block {
			margin-bottom: 50px;

			@include min-w(md) {
				margin-bottom: 80px;
			}
		}

		&__play-btn-block {
			margin-bottom: 40px;

			@include min-w(sm) {
				margin-bottom: 50px;
			}

			@include min-w(lg) {
				margin-bottom: 80px;
			}
		}
	}

	&-welcome-for-quiz {
		padding: 50px 0;
		overflow: hidden;

		@include min-w(md) {
			padding: 80px 0 100px;
		}

		@include min-w(lg) {
			padding: 80px 0 150px;
		}

		&__text-block {
			text-align: center;
			margin-bottom: 50px;
		}

		&__subtitle {
			color: $primary;
			font-weight: 700;
		}

		&__description {
			max-width: 800px;
			margin: 0 auto;
			font-weight: 300;
		}
	}

	&-logos {
		padding: 10px 0;
		background-color: $gray-300;

		@include min-w(sm) {
			padding: 15px 0;
		}

		@include min-w(xl) {
			padding: 30px 0;
		}

		.container-fluid {
			@include max-w(md) {
				padding: 0;
			}
		}
	}

	&-text-article {
		&__content {
			max-width: 900px;
			margin: 0 auto;
		}

		&__main-content {
			margin-bottom: 50px;

			p {
				font-weight: 300;

				@include min-w(md) {
					margin-bottom: 30px;
				}
			}

			strong {
				@extend .warning-text;
			}
		}

		&__btn-block {
			text-align: center;
		}
	}

	&-works {
		background-color: $black;
	}

	&-with-icon-items {
		&__main-block {
			margin-bottom: 30px;

			@include min-w(md) {
				margin-bottom: 80px;
			}
		}

		&__btn-block {
			text-align: center;
		}
	}

	&-with-images {
		position: relative;
		padding: 50px 0;
		background-color: $blue-dark;

		@include min-w(sm) {
			padding: 70px 0;
		}

		@include min-w(md) {
			padding: 100px 0;
		}

		@include min-w(xl) {
			padding: 100px 0 170px;
		}

		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 20%;
			max-height: 360px;
			background-color: $gray-300;

			@include min-w(sm) {
				height: 30%;
				max-height: 360px;
			}
		}

		&__content {
			position: relative;
			z-index: 5;
		}

		&__images-grid {
			margin-bottom: 30px;
			overflow: hidden;

			@include min-w(sm) {
				margin-bottom: 70px;
			}

			@include min-w(md) {
				margin-bottom: 100px;
			}

			@include min-w(xl) {
				margin-bottom: 180px;
			}

			img {
				box-shadow: 0 55px 80px -40px rgba($black, 0.45);
				margin-bottom: 20px;

				@include min-w(sm) {
					margin-bottom: 0;
				}
			}

			.row {
				@include max-w(sm) {
					margin-left: -10px;
					margin-right: -10px;
				}

				[class^="col-"] {
					@include max-w(sm) {
						padding-left: 10px;
						padding-right: 10px;
					}
				}
			}
		}

		&__text-content {
			color: $white;
			text-align: center;
		}
	}

	&-with-videos {
		background-color: $gray-800;
		color: $white;

		&__title-block {
			max-width: 1000px;
			margin: 0 auto 30px;
			text-align: center;

			@include min-w(md) {
				margin-bottom: 50px;
			}
		}

		.row {
			[class^="col-"] {
				&:not(:last-child) {
					margin-bottom: 30px;

					@include min-w(md) {
						margin-bottom: 0;
					}
				}
			}
		}
	}

	&-progress-bar {
		@extend .top-indent-for-header;

		&__content {
			padding: 10px 0;
			background-color: $gray-300;

			@include min-w(lg) {
				padding: 15px 0;
			}
		}
	}
}