input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
input[type='url'],
select,
textarea {
	@include placeholder {
		color: #6f706f;
	};

	display: block;
	width: 100%;
	border: 2px solid $gray-400;
	border-radius: 40px;
	background-color: #f2f2f2;
	padding: 10px;
	font-size: 16px;
	font-weight: 300;
	line-height: 22px;
	color: $black;
	transition: border-color 0.3s;

	@include min-w(lg) {
		padding: 16px;
		font-size: 18px;
	}

	&:focus {
		border-color: $gray-500;
	}

	&.error {
		border-color: $red;
	}

	&[name*="name"], &[name*="Name"] {
		background-image: url("img/icons/avatar.png");
		background-repeat: no-repeat;
		background-position: 8px center;
		background-size: 30px;
		padding-left: 45px;

		@include min-w(lg) {
			padding-left: 60px;
			background-size: 39px;
		}
	}

	&[name*="email"], &[name*="Email"] {
		background-image: url("img/icons/email.png");
		background-repeat: no-repeat;
		background-position: 10px center;
		background-size: 25px;
		padding-left: 45px;

		@include min-w(lg) {
			padding-left: 60px;
			background-position: 15px center;
			background-size: 32px;
		}
	}
}

input[type="submit"] {
	@extend .btn-primary;

	border-radius: 40px;
	border: none;
	transition: color 0.3s, background-color 0.3s;
	font-size: 16px;
	padding: 9px 10px;

	@include min-w(lg) {
		font-size: 20px;
		padding: 11px 15px;
	}
}

textarea {
	border-radius: 5px;
	height: 250px;
}

.standart-checkbox,
.standart-radio {
	position: relative;
	display: block;
	width: 100%;
	margin-bottom: 0;

	&:hover {
		.standart-checkbox__item,
		.standart-radio__item {
			background-color: darken($primary, 5%);
		}
	}

	&__input {
		@extend .-visually-hidden;

		&:checked {
			+ .standart-checkbox__item,
			+ .standart-radio__item {
				background-color: darken($primary, 20%);

				.standart-checkbox__icon,
				.standart-radio__icon {
					border-color: $white;

					&:after {
						opacity: 1;
					}
				}
			}
		}

		&.error {
			+ .standart-checkbox__item,
			+ .standart-radio__item {
				background-color: $red;
			}
		}
	}

	&__item {
		position: relative;
		padding: 10px 10px 10px 40px;
		font-size: 14px;
		background-color: $primary;
		color: $white;
		cursor: pointer;
		transition: background-color 0.3s;
		border-radius: 5px;

		@include min-w(md) {
			padding: 15px 15px 15px 60px;
			font-size: 16px;
		}
	}

	&__icon {
		@include sizeElement(20px);

		position: absolute;
		top: 50%;
		left: 10px;
		transform: translate(0, -50%);
		border-radius: 50%;
		border: 2px solid rgba($white, 0.5);
		transition: border-color 0.3s;

		@include min-w(md) {
			@include sizeElement(30px);

			border-width: 3px;
			left: 15px;
		}

		&:after {
			@include absoluteCenter;

			content: '\f00c';
			font-family: "Font Awesome 5 Free";
			font-weight: 600;
			font-style: normal;
			font-size: 10px;
			line-height: 1;
			opacity: 0;
			transition: opacity 0.3s;

			@include min-w(md) {
				font-size: 13px;
			}
		}
	}
}

.checkbox-with-icon,
.radio-with-icon {
	display: block;
	width: 100%;
	margin-bottom: 0;

	&:hover {
		.checkbox-with-icon__item,
		.radio-with-icon__item {
			background-color: darken($primary, 5%);
		}
	}

	&__input {
		@extend .-visually-hidden;

		&:checked {
			+ .checkbox-with-icon__item,
			+ .radio-with-icon__item {
				background-color: darken($primary, 20%);
			}
		}

		&.error {
			+ .checkbox-with-icon__item,
			+ .radio-with-icon__item {
				background-color: $red;
			}
		}
	}

	&__item {
		padding: 10px;
		font-size: 14px;
		background-color: $primary;
		color: $white;
		cursor: pointer;
		transition: background-color 0.3s;
		border-radius: 5px;
		text-align: center;

		@include min-w(md) {
			font-size: 16px;
		}

		@include min-w(lg) {
			padding: 20px;
			font-size: 18px;
		}
	}

	&__icon {
		@include sizeElement(35px);
		@include flexCenter;

		font-size: 20px;
		margin: 0 auto 5px;
		line-height: 1;

		@include min-w(md) {
			@include sizeElement(50px);

			font-size: 30px;
			margin-bottom: 10px;
		}

		img {
			max-height: 100%;
			flex-shrink: 0;
		}
	}
}

.irs--flat {
	@include max-w(md) {
		height: 50px;
	}

	.irs-bar {
		top: 30px;
		height: 10px;
		border-radius: 10px;
		background-color: $primary;

		@include min-w(md) {
			top: 10px;
			height: 20px;
			border-radius: 20px;
		}
	}

	.irs-line {
		top: 30px;
		height: 10px;
		border-radius: 10px;
		background-color: #e6e6e6;

		@include min-w(md) {
			top: 10px;
			height: 20px;
			border-radius: 20px;
		}
	}

	.irs-handle {
		@include sizeElement(30px);

		background-color: #fbfbfb;
		box-shadow: 0 0 15px rgba($black, 0.15);
		border: 1px solid #cccccc;
		border-radius: 50%;
		top: 20px;

		i {
			display: none !important;
		}

		@include min-w(md) {
			top: 0;
			display: none;
		}
	}

	.irs-single {
		color: $black;
		font-size: 16px;
		font-family: $font-family-sans-serif;
		font-weight: 600;
		z-index: 1;
		line-height: 1.3;
		background-color: transparent;
		top: 0;

		@include max-w(md) {
			left: 50% !important;
			transform: translate(-50%, 0);
		}

		@include min-w(md) {
			padding: 7px 45px;
			font-size: 19px;
			background-color: #fbfbfb;
			border-radius: 40px;
			box-shadow: 0 0 15px rgba($black, 0.15);
			border: 1px solid #cccccc;
			cursor: pointer;
		}

		&:before, &:after {
			position: absolute;
			top: 50%;
			transform: translate(0, -50%);
			display: inline-block;
			font-style: normal;
			font-family: 'Font Awesome 5 Free';
			font-weight: 900;
			color: #cccccc;

			@include max-w(md) {
				content: none !important;
			}
		}

		&:before {
			content: '\f053';
			left: 10px;
			border: none;
			bottom: auto;
			width: auto;
			height: auto;
			margin-left: 0;
		}

		&:after {
			content: '\f054';
			right: 10px;
		}
	}
}

.aweber-form {
	&.-white {
		input {
			&[type='text'] {
				border-color: $white;
				color: $white;
				background-color: transparent;
				box-shadow: none;

				&:focus {
					border-color: #999999;
				}
			}

			&[type='submit'] {
				@extend .btn-white;
			}
		}
	}

	.previewLabel, .af-header {
		display: none;
	}

	.af-body {
		@include min-w(md) {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -15px;
		}
	}

	.af-element {
		&:not(:last-child) {
			margin-bottom: 15px;
		}

		@include min-w(md) {
			flex: 0 0 33.333333%;
			max-width: 33.333333%;
			padding: 0 15px;
			margin-bottom: 0;
		}
	}

	input {
		&[type='text'] {
			padding-left: 45px;

			@include min-w(lg) {
				padding-left: 60px;
			}
		}

		&[type="submit"] {
			display: block;
			width: 100%;
		}
	}
}
