.btn {
	font-family: $font-family-heading;
	text-transform: uppercase;
	box-shadow: none !important;

	@include max-w(md) {
		font-size: 20px;
	}

	@include max-w(sm) {
		font-size: 16px;
	}

	&-sm {
		@include max-w(md) {
			font-size: $font-size-sm;
		}

		@include min-w(md) {
			min-width: 180px;
		}
	}

	&-lg {
		@include max-w(lg) {
			font-size: 24px;
			line-height: 1.1;
		}
	}

	&-gray {
		color: $black;
		background-color: $gray-300;

		&:hover {
			background-color: darken($gray-300, 10%);
		}
	}

	&-white {
		color: $black;
		background-color: $white;

		&:hover {
			background-color: $primary;
			color: $white;
		}
	}

	&.-with-under-text {
		@include min-w(lg) {
			padding: 10px 100px;
		}

		span {
			display: block;
			font-family: $font-family-sans-serif;
			font-weight: 300;
			font-size: 12px;
			color: darken($primary, 50%);
			padding-top: 5px;

			@include min-w(lg) {
				padding-top: 0;
			}
		}
	}
}

.play-btn {
	@include sizeElement(50px);

	position: relative;
	display: block;
	margin: 0 auto;
	border-radius: 50%;
	background-color: $white;
	box-shadow: 0 0 0 7px rgba($primary, 0.5);
	transition: box-shadow 0.5s, transform 0.5s;

	@include min-w(md) {
		@include sizeElement(75px);
	}

	&:after {
		@include absoluteCenter;
		@include border-arrow(14, 18, $black, r);

		@include min-w(md) {
			@include border-arrow(17, 21, $black, r);
		}

		content: '';
		display: block;
		margin-left: 2px;
	}

	&:hover {
		box-shadow: 0 0 0 10px rgba($primary, 0.5);
		transform: scale(1.2);
	}
}

.fancybox-button {
	@extend .btn-white;

	transition: color 0.3s, background-color 0.3s;

	svg path {
		fill: $black;
		transition: fill 0.3s;
	}

	&:hover {
		svg path {
			fill: $white;
		}
	}
}