.item-with-icon {
	margin-bottom: 40px;
	text-align: center;

	&__icon {
		@include sizeElement(60px);
		@include flexCenter;

		margin: 0 auto 20px;

		@include min-w(xl) {
			@include sizeElement(80px);

			margin-bottom: 40px;
		}

		img {
			max-height: 100%;
			flex-shrink: 1;
		}
	}

	&__description {
		font-weight: 300;
		font-size: 16px;

		@include min-w(md) {
			font-size: 18px;
		}
	}
}