.works-slider {
	margin-bottom: 0 !important;

	.slick-dots {
		position: static;
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		padding-top: 50px;

		@include min-w(sm) {
			padding-top: 80px;
		}

		@include min-w(xl) {
			padding-top: 100px;
		}
	}

	.slick-slide {
		padding: 0 15px;

		@include min-w(md) {
			padding: 0 30px;
		}

		@include min-w(xl) {
			padding: 0 70px;
		}
	}
}

.works-slider-item {
	color: $white;

	&__image {
		@include responsive-ratio(16, 9);
		@include bgCover;

		display: block;
		margin-bottom: 15px;

		@include min-w(sm) {
			margin-bottom: 30px;
		}
	}

	&__text-block {
		text-align: center;
	}

	&__title {
		a {
			color: $white;
			transition: color 0.3s;

			&:hover {
				color: $primary;
			}
		}
	}

	&__description {
		font-size: 14px;
		font-weight: 300;
		max-width: 500px;
		margin: 0 auto;

		@include min-w(sm) {
			font-size: 16px;
		}
	}
}